import React, {useEffect, useState} from "react";
import './App.css';
import JSON5 from "json5";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import ProjectPanel from "./components/PorjectPanel";

const ImageSlider = (props) => {
    const {screenshots} = props;
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };


    if (!screenshots) return null;

    if (screenshots.length === 1) {
        return <img style={{
            width: "800px",
            // height: '600px',
        }} src={`./${screenshots[0]}`}/>

    }

    return <div style={{
        border: '1px solid black',
        width: "800px",
        // height: '600px',
        marginBottom: '1em',
    }}>
        <Slider {...settings}>

            {
                screenshots.map(screenshot => <div style={{
                    // width: "800px",
                    // height: '600px',
                }}>
                    <img style={{
                        width: "800px",
                        height: '600px',
                        objectFit: "contain",
                    }} src={`./${screenshot}`}/>
                </div>)
            }
        </Slider>
    </div>
}

function App() {
    const [projects, setProjects] = useState(null);

    useEffect(() => {
        (async () => {
            try {
                const response = await fetch('projects.json');
                const text = await response.text();
                const data = JSON5.parse(text);
                console.log(data.items);
                setProjects(data.items);
            } catch (error) {
                console.error('JSON  :', error);
            }
        })();
    }, []);

    if (!projects) return null;

    return (
        <div className="App">
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                fontWeight: 'bold',
                position: 'fixed',
                left: 0,
                top: 0,
                margin: "0.5em",
            }}>
                {
                    projects.map(project => <a style={{
                        cursor: 'pointer',
                    }} href={`#${project.title}`} key={project.title}>{
                        project.title
                    }</a>)
                }
            </div>
            <div style={{
                position: 'relative',
                width: '800px',
                display: 'flex',
                justifyContent: 'center',
                // alignItems: 'center',
                margin: "0.3em",
                fontWeight: 'bold',
                textShadow: '2px 2px 2px #bbb',
            }}>Won's projects
                <a href="https://github.com/juwoni" style={{
                    position: 'absolute',
                    right: "0",
                }}>
                    <img style={{
                        width: '32px',
                        height: '32px',
                    }} src="./icons/github.png"/>
                </a>
            </div>
            {
                projects.map((project, index) => {
                    const {title, stack, date, screenshots, description, url, urlComment} = project;
                    return <ProjectPanel key={title} data={{
                        title,
                        stack,
                        date,
                        url,
                        urlComment,
                        screenshots,
                        description,
                    }}/>
                })
            }
        </div>
    );
}


export default App;
