import React from "react";
import * as PropTypes from "prop-types";
import Slider from "react-slick";

const ImageSlider = (props) => {
    const {screenshots} = props;
    const settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };


    if (!screenshots) return null;

    if (screenshots.length === 1) {
        return <img style={{
            width: "800px",
            // height: '600px',
        }} src={`./${screenshots[0]}`}/>

    }

    return <div style={{
        border: '1px solid black',
        width: "800px",
        // height: '600px',
        marginBottom: '1em',
    }}>
        <Slider {...settings}>

            {
                screenshots.map((screenshot, index) => <div key={index} style={{
                    // width: "800px",
                    // height: '600px',
                }}>
                    <img style={{
                        width: "800px",
                        height: '600px',
                        objectFit: "contain",
                    }} src={`./${screenshot}`}/>
                </div>)
            }
        </Slider>
    </div>
}

const ProjectPanel = (props) => {
    const {title, date, url, urlComment, stack, description, screenshots} = props.data;


    return <div id={`${title}`} style={styles.panel}>
        <div style={{
            fontSize: '1.5em',
            fontWeight: 'bold',
            textShadow: '2px 2px 2px #bbb',

        }}>{title}</div>
        {date && <div style={{
            fontWeight: 'bold',
        }}>{date}</div>}
        {
            url && <div>
                <a href={url}>
                    {url}
                </a>
                {urlComment && <span style={{color: 'white'}}> {urlComment}</span>}
            </div>
        }

        <div style={{
            display: 'flex',
            margin: "0.3em",
            fontSize: "0.7em",
            fontWeight: 'bold',
            color: "lightskyblue"
        }}>
            {stack.map((_stack, index) => <div key={_stack} style={{
                marginRight: '0.3em',
            }}>
                {_stack}{index < stack.length - 1 ? "," : ""}
            </div>)}
        </div>

        <ImageSlider screenshots={screenshots}/>

        <div style={{
            width: '800px',
            padding: '0.3em',
            fontSize: '0.7em',
            whiteSpace: "pre-wrap",
            // textDecoration: isStrike ? 'line-through' : 'none',
            textAlign: "left",
            // alignContent: "flex-start"
            // aligen
        }}>
            {description && description.map((line, index) => {
                const isStrike = typeof line === 'string' && line.startsWith('~');
                const content = isStrike ? line.slice(1) : line;

                return <div key={index} style={{
                    textDecoration: isStrike ? 'line-through' : 'none',
                }}><span>{" "}</span>{content}</div>
            })}
        </div>
    </div>
}

const styles = {
    panel: {
        // backgroundColor : "rgba(250, 235, 215,0.5)",
        borderRadius: '20px',
        backgroundColor: '#42464e',
        // boxShadow: "2px 2px 2px gray",

        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: '1em',
        marginBottom: '1em',

        // border: '1px solid white'
    }
}

export default ProjectPanel;